<template>
  <div class="top" :style="{ display: display }">
    <div>
      <div class="header">
        <div
          v-for="(item, index) of materialList"
          :key="item.materialType"
          :class="active == index ? 'active' : 'title'"
        >
          <div class="pdfName">
            {{ item.materialTypeDesc || item.materialName }}
            <img v-show="item.read" class="affirm" src=~@/assets/image/check.png
            alt="" srcset="">
          </div>
        </div>
      </div>

      <div class="pdf" v-if="!showWord">
        <div
          v-if="
            pdfs[pdfIndex] &&
            pdfs[pdfIndex].materialType &&
            pdfs[pdfIndex].materialType == 'M01'
          "
          class="description"
        >
          产品条款:{{ pdfs[pdfIndex] && pdfs[pdfIndex].description }}
        </div>
        <pdf
          :src="pdfsrc"
          :page="currentPage"
          @num-pages="pageCount = $event"
          @loaded="currentPage = 1"
        ></pdf>
        <div class="pdfpage" v-if="!showWord">
          <span @click="currentPage > 1 && currentPage--">上一页</span>
          <span>当前第{{ currentPage || 0 }}页/共{{ pageCount || "∞" }}页</span>
          <span @click="currentPage < pageCount && currentPage++">下一页</span>
        </div>
      </div>
      <div class="pdf" v-if="showWord">
        <div class="word-content" style="font-weight: bold">{{ wordTip }}</div>
        <div
          class="word-content"
          v-for="(content, index) in words"
          v-html="content.content || content"
          :key="content + index"
        ></div>
      </div>
    </div>
    <div class="button">
      <van-button class="but2" type="info" @click="next(++pdfIndex, hasAgree)"
        >我已逐页阅读并同意（{{
          read.length + "/" + materialList.length
        }}）</van-button
      >
    </div>
  </div>
</template>

 
 <script>
import pdf from "vue-pdf";
import { mapState, mapActions } from "vuex";
import util, {
  clearParams,
  getRelationShip,
  getlastDay,
  getDay,
  reconfigArr,
} from "@/components/_utils";
export default {
  name: "",
  components: { pdf },

  data() {
    return {
      display: "block",
      productData: JSON.parse(sessionStorage.getItem("productData")),
      ENCR: null,
      active: 0,
      SINGE_PAGE_from: JSON.parse(
        sessionStorage.getItem("SINGE_PAGE_from")
      ) || {
        userName: "",
        idCard: "",
        dateTime: "",
        phone: "",
        SMSCode: "",
        school: "",
        gradeChoose: "",
        classChoose: "",
        email: "",
        area: "",
      },
      MORE_PAGE_from: JSON.parse(sessionStorage.getItem("MORE_PAGE_from")) || {
        userName: "",
        idCard: "",
        dateTime: "",
        userNameInsured: "",
        idCardInsured: "",
        dateTimeInsured: "",

        phone: "",
        SMSCode: "",
        school: "",
        gradeChoose: "",
        classChoose: "",
        email: "",
        area: "",
      },
      showPdf: false,
      pdfsrc: "",
      pdfIndex: 0,
      pdfs: [],
      currentPage: 0,
      pageCount: 0,
      showWord: false,
      hasAgree: false,
      info: { choosedRela: "" },
      active: 0,
      page: 1,
      clientHeight: document.documentElement.clientHeight - 150,
      //   设置能不能点击tabs的
      pointerEvents: true,
      previousPageDisabled: true,
      materialList: [], //产品条款
      exonerationNoticeCardRisk: {}, //免责条款
      content: [],
      read: [0],
    };
  },
  created() {},
  mounted() {
    this.ENCR = JSON.parse(sessionStorage.getItem("Risk"));
    this.exonerationNoticeCardRisk = JSON.parse(
      sessionStorage.getItem("exonerationNoticeCardRisk")
    );
    if (sessionStorage.getItem("route_radio") == 1) {
      this.info.choosedRela = "01";
    } else {
      console.log(this.SINGE_PAGE_from);
      console.log(this.MORE_PAGE_from);
      this.info.choosedRela = "04";
    }
    console.log(JSON.parse(sessionStorage.getItem("pdfList")).length);
    if (JSON.parse(sessionStorage.getItem("pdfList")).length > 0) {
      this.materialList = JSON.parse(sessionStorage.getItem("pdfList"));
      this.materialList.map((item, index) => {
        if (
          (item.materialType && item.materialType == "M08") ||
          item.materialType == "M11"
        ) {
          this.materialList.splice(index, 1);
        }
      });
      this.pdfPreview(0);
    } else {
      this.display = "none";

      this.checkHold(this.params);
    }
  },
  methods: {
    ...mapActions([
      "checkHold",
      "cardCollectionImport",
      "mobileCheck",
      "orderInfoUpdate",
    ]),
    next() {
      if (this.read.length >= this.materialList.length) {
        this.materialList[this.materialList.length - 1].read = true;
        this.checkHold(this.params);
        return;
      }
      this.materialList.map((item, index) => {
        this.content.push(index);
      });
      let see = [];
      this.content.find((v) => {
        if (!this.read.includes(v)) {
          this.active = v;
          see.push(v);
        }
      });
      this.previewPdf((this.active = see[0]));
    },
    previewPdf(index, once) {
      this.active = index;
      this.read = Array.from(new Set(this.read));
      if (index >= this.pdfs.length) {
        return (this.showPdf = false), (this.hasAgree = true);
      }
      this.pdfIndex = index;
      this.showPdf = true;
      const material = this.pdfs[index];
      document.title = "产品条款";
      if (!once) {
        this.read.push(index);
        if (index == 0) return;
        this.materialList[index - 1].read = true;
      }
      this.showWord = false;
      if (this.pdfs[index].notPdf) {
        this.pdfsrc = "";
        this.words = material.content;
        return (this.showWord = true);
      }

      const BASE_URL = process.env.VUE_APP_BASE_URL || "";
      const baseUrl =
        location.origin +
        location.pathname.replace("/elife-card", "") +
        BASE_URL;
      const path = material.materialPath;
      this.pdfsrc = pdf.createLoadingTask(baseUrl + "images" + path);
    },
    pdfPreview(index) {
      const productMaterialDetail = reconfigArr(this.materialList, index);
      Promise.all(
        productMaterialDetail.map((v) => {
          if (v.materialType) {
            return this.$api.common.getProductMaterialDetail({
              prdId: this.productData.prd_id,
              ...v,
            });
          } else {
            return v;
          }
        })
      ).then((materialList) => {
        console.log(materialList, "materialList");
        // 过滤出掉接口的
        let p = materialList.filter((e) => {
          return e.status == 1;
        });
        console.log(p);
        // 判断上面过滤的数组是否里面都为空
        let p2 = p.every((e) => {
          return e.data.length == 0;
        });
        console.log(p2);

        if (!p2) {
          this.pdfs = materialList.reduce((init, res) => {
            let item = [];
            if (res.status === "1") {
              item = res.data.length ? res.data : [res.data];
            } else if (res.notPdf) {
              item = [res];
            } else {
              item = [];
            }

            return init.concat(item);
          }, []);
          this.previewPdf(0, 1);
          this.$loading.hide();
        } else {
          this.display = "none";
          this.checkHold(this.params);
        }
      });
    },
    // 点击上一页的
    previousPage() {
      this.page--;
      if (this.page <= 1) {
        this.previousPageDisabled = true;
        this.page = 1;
      } else {
        this.previousPageDisabled = false;
      }
      this.active = this.page - 1;
    },
    checkstand() {
      const toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "即将进入收银台",
      });

      let second = 3;
      const timer = setInterval(() => {
        second--;
        if (second) {
          toast.message = ` ${second} 秒后完成支付`;
        } else {
          clearInterval(timer);
          // 手动清除 Toast
          this.$toast.clear();
        }
        if (second == 0) {
          this.$toast.success("支付成功");
          this.$router.push({
            path: "/cardResults",
          });
        }
      }, 1000);
    },
  },
  watch: {
    // 页数的变化
    page(a, b) {
      console.log(a, b);
      if (a > 1) {
        this.previousPageDisabled = false;
      }
    },
  },
  computed: {
    wordTip() {
      const word = this.pdfs[this.pdfIndex];
      const enrc = this.ENCR;
      const plans = this.productData.plans;
      console.log(enrc, "enrc");
      if (word && word.notPdf) {
        const planCodes = plans.map((v) => v.planCode.slice(0, -2));
        const risks = plans.map(
          (v) =>
            enrc.find((item) => item.risk === v.planCode.slice(0, -2)).riskName
        );
        const HI = enrc
          .filter((v) => planCodes.includes(v.risk) && v.isHI)
          .sort((a, b) => planCodes.indexOf(a.risk) - planCodes.indexOf(b.risk))
          .map((v) => v.riskName);
        if (word.type === "exoneration") {
          return `本产品计划由《${risks.join("》、《")}》组成，${
            word.materialName
          }如下：`;
        } else {
          console.log("HI", HI);
          const HItip =
            HI.length > 0 ? `。其中《${HI.join("》、《")}》为健康保险产品` : "";
          return `本产品计划由《${risks.join("》、《")}》组成${HItip}，${
            word.materialName
          }如下：`;
        }
      } else {
        return "";
      }
    },
    params() {
      console.log(this.info);
      let batchNo =
        this.productData.batchNo || this.batch.batchNo || this.batchNum || "";
      let channel = "";
      if (location.href.indexOf("channel") != -1) {
        channel = location.href.split("=")[1];
      } else {
        channel = this.channelCode;
      }
      let qwyChannel = sessionStorage.getItem("channelCode");
      if ("0037" === qwyChannel) {
        channel = qwyChannel;
      }
      console.log(channel);
      batchNo = batchNo.length > 3 ? batchNo : "";
      let card = this.batch.cardPwd ? { plchdCardPwd: this.batch.cardPwd } : {};

      let Time = JSON.parse(sessionStorage.getItem("planPage"));

      let insValiDate =
        this.productData.takeEffectFlag == 1
          ? `${Time.time.slice(0, 4)}` +
            `-${Time.time.slice(4, 6)}` +
            `-${Time.time.slice(6, 8)}`
          : `${Time.time.slice(0, 4)}` +
            `-${Time.time.slice(4, 6)}` +
            `-${Time.time.slice(6, 8)}` +
            ` 24:00:00`; //保险生效期
      return {
        ...{
          prdId: this.productData.prd_id, //产品id
          orderCode: sessionStorage.getItem("SINGE_PAGE_orderfode"), //订单号
          plchdCardNo:
            JSON.parse(sessionStorage.getItem("insuranceInformation_PAGE"))
              .plchdCardNo || "", //卡号
          insPolcyIntndEfDt: JSON.parse(sessionStorage.getItem("planPage"))
            .time, //保单生效日

          plchdBrthDt:
            this.info.choosedRela == "01"
              ? util.getBirthday(this.SINGE_PAGE_from.idCard)
              : util.getBirthday(this.MORE_PAGE_from.idCard), //投保人生日YYYYMMDD
          plchdCrdtExpDt:
            this.info.choosedRela == "01"
              ? clearParams(
                  this.SINGE_PAGE_from.dateTime == "长期有效"
                    ? 99991231
                    : this.SINGE_PAGE_from.dateTime
                )
              : // : clearParams(this.MORE_PAGE_from.dateTime.replace(/-/g, "")), //证件失效时间YYYYMMDD
                clearParams(
                  this.MORE_PAGE_from.dateTime == "长期有效"
                    ? 99991231
                    : this.MORE_PAGE_from.dateTime
                ), //证件失效时间YYYYMMDD

          plchdCrdtNo:
            this.info.choosedRela == "01"
              ? clearParams(this.SINGE_PAGE_from.idCard)
              : clearParams(this.MORE_PAGE_from.idCard), //身份证号
          plchdCrdtTpCd: "1010", //证件类型                                                   //A修改为1010
          plchdEmailAdr:
            this.info.choosedRela == "01"
              ? clearParams(this.MORE_PAGE_from.email)
              : clearParams(this.MORE_PAGE_from.email), //邮件
          plchdGndCd:
            this.info.choosedRela == "01"
              ? util.getSex(this.SINGE_PAGE_from.idCard)
              : util.getSex(this.MORE_PAGE_from.idCard), //性别
          plchdMoveTelNo:
            this.info.choosedRela == "01"
              ? clearParams(this.SINGE_PAGE_from.phone)
              : clearParams(this.MORE_PAGE_from.phone), //手机号
          plchdNatCd: "156", //国籍                                                          //0156修改为156
          plchdNm:
            this.info.choosedRela == "01"
              ? clearParams(this.SINGE_PAGE_from.userName)
              : clearParams(this.MORE_PAGE_from.userName), //投保人姓名

          plchdAndRcgnReTpCd: getRelationShip(
            this.info.choosedRela,
            util.getSex(this.MORE_PAGE_from.idCard || ""),
            util.getSex(this.MORE_PAGE_from.idCardInsured || "")
          ), //投被保人关系
          rcgnBrthDt:
            this.info.choosedRela == "01"
              ? util.getBirthday(this.SINGE_PAGE_from.idCard)
              : util.getBirthday(this.MORE_PAGE_from.idCardInsured), //被保人生日YYYYMMDD
          rcgnCrdtExpDt:
            this.info.choosedRela == "01"
              ? clearParams(
                  this.SINGE_PAGE_from.dateTime == "长期有效"
                    ? 99991231
                    : this.SINGE_PAGE_from.dateTime
                )
              : clearParams(
                  this.MORE_PAGE_from.dateTimeInsured == "长期有效"
                    ? 99991231
                    : this.MORE_PAGE_from.dateTimeInsured
                ), //被保人证件失效时间YYYYMMDD
          rcgnCrdtNo:
            this.info.choosedRela == "01"
              ? clearParams(this.SINGE_PAGE_from.idCard)
              : clearParams(this.MORE_PAGE_from.idCardInsured), //被保人身份证号
          rcgnCrdtTpCd: "1010", //被保人证件类型
          rcgnEmail:
            this.info.choosedRela == "01"
              ? clearParams(this.MORE_PAGE_from.email)
              : clearParams(this.MORE_PAGE_from.email), //被保人邮件
          rcgnGndCd:
            this.info.choosedRela == "01"
              ? util.getSex(this.SINGE_PAGE_from.idCard)
              : util.getSex(this.MORE_PAGE_from.idCardInsured), //性别
          rcgnNatCd: "156", //国籍
          rcgnNm:
            this.info.choosedRela == "01"
              ? this.SINGE_PAGE_from.userName
              : this.MORE_PAGE_from.userNameInsured, //被保人名
          //todo:
          // channelCode: channel == '' ? this.channelCode : '', //销售渠道
          channelCode: channel == "" ? this.channelCode : channel, //销售渠道
          plchdProvCd: this.productData.plchdProvinceCode, //省350000 210000.211300.211303
          plchdCityCd: this.productData.plchdCityCode, //市350100
          plchdCntyAndDstcCd: this.productData.plchdDistrictCode, //区350103
          rsdntTpCd: this.productData.plchdCityFlag, //this.productData.rsdntTpCd, //城市/农村0
          appNoNum: this.productData.plchdPrtCount, //需要投保单号的数量
          totInsPremAmt: this.productData.initPyFAmt, //个人付费金额
          coPyFAmt: this.productData.coPyFAmt, //企业付费金额
          interfaceCode: this.productData.funcFlag, //业务编码
          channel: channel,
          batchNo, // 批次号
          classId:
            this.info.choosedRela == "01"
              ? this.SINGE_PAGE_from.classId
              : this.MORE_PAGE_from.classId, // 班级id

          // 添加--传日期给后端判断
          insurear: this.productData.plans[0].insuYear, //保险期间
          insurearflag: this.productData.plans[0].insuYearFlag, //保险期间单位[Y-年、M-月、D-天、A-年龄]
          insValiDate, //保险生效期
        },
        ...card,
      };
    },
    ...mapState({
      userCode: (state) => state.app.userCode,
      phone: (state) => state.app.phone,
      userInfo: (state) => state.app.policyInfo,
      hasUserInfo: (state) => state.app.hasUserInfo,
      hasSomeUserInfo: (state) => state.app.hasSomeUserInfo,
      batch: (state) => state.app.batch,
      channelCode: (state) => state.app.channelCode,
      minDate: (state) => state.app.minDate,
      maxDate: (state) => state.app.maxDate,
      batchInfo: (state) => state.app.batchInfo,
      importActiveFlag: (state) => state.app.importActiveFlag,
      checkPhoneID: (state) => state.app.checkPhoneID,
      hasCheckHold: (state) => state.app.hasCheckHold,
      batchNum: (state) => state.app.batchNo,
      routeQuery: (state) => state.app.routeQuery,
    }),
  },

  //  // 卡单产品信息  详情页面缓存的
  productData() {
    return JSON.parse(sessionStorage.getItem("productData"));
  },
};
</script>
 
 <style lang='less' scoped>
.word-content {
  font-size: 14px;
  color: #303133;
  text-align: left;
  padding: 3px 15px;
  line-height: 1.5em;
  h4 {
    margin: 1em 0 !important;
  }
}
.header {
  overflow-x: auto;
  display: flex;
  border-bottom: 1px solid #ccc;
  font-size: 16px;
  flex-wrap: nowrap;
  box-sizing: border-box;
  justify-content: space-between;
}
.active {
  color: #333;
  width: 25vw;
  height: 10vw;
  line-height: 10vw;
  text-align: center;
  position: relative;
  flex: 1 0 auto;
  flex-wrap: nowrap;
  color: #4ca2fe;
}
.active::after {
  position: absolute;
  content: "";
  width: 50%;
  bottom: 0;
  height: 1px;
  background: #4ca2fe;
  left: 25%;
}
.title {
  color: #333;
  width: 25vw;
  height: 10vw;
  line-height: 10vw;
  text-align: center;
  flex: 1 0 auto;
  flex-wrap: nowrap;
}
.pdfName {
  position: relative;
}
.affirm {
  width: 6vw;
  height: 6vw;
  position: absolute;
  right: 0;
  top: 0;
}
.top {
  padding: 10px;
}
.pdfNew {
  margin-top: 20px;
  width: 100%;
  overflow: auto;
  border: #1989fa solid 1px;
}

:deep(.van-tab--active) {
  color: #1989fa;
  font-weight: 600;
}

:deep(.van-tabs__line) {
  position: absolute;
  bottom: 15px;
  left: 0;
  z-index: 1;
  width: 40px;
  height: 3px;
  background-color: #1989fa;
  border-radius: 3px;
}
.but {
  width: 40%;
  position: fixed;
  left: 10px;
  bottom: 10px;
  border-radius: 18px;
}
.but2 {
  // width: 40%;
  width: 90vw;
  margin: 0 auto;
  border-radius: 18px;
  margin-bottom: 10px;
}
// 你可以看的到某个元素，但是你无法摸的着。
:deep(.van-tab) {
  pointer-events: none;
}
.pdf-pointer {
  pointer-events: auto !important;
}
.button {
  background: #fff;
  height: 14vw;
  position: fixed;
  bottom: 0;
  margin: 0 auto;
  width: 100vw;
}
</style>
 